export default {
  name: 'NormalDialog',
  data () {
    return {
      dialog: false,
      message: null,
      title: null,
      options: {
        width: 400,
        zIndex: 200,
        noconfirm: false
      }
    }
  },

  methods: {
    open (title, message) {
      this.dialog = true
      this.title = title
      this.message = message
    }
  }
}
